import { reactive, readonly } from 'vue';
import api from '@/store/modules/warpnet/actions';

const stateMap = reactive(new Map());
const ttlMs = 60_000; // 60 sec
const defaultLimit = 20
const endCursor = "end"

export const reactiveState = {
    state: readonly(stateMap),

    clear() {
        stateMap.clear();
    },

    invalidate(prefix) {
        for (const key of stateMap.keys()) {
            if (key.startsWith(prefix)) {
                stateMap.delete(key);
            }
        }
    },

    setCursor(key, cursor) {
        const cacheKey = `${key}::cursor`;
        stateMap.set(cacheKey, cursor)
    },

    getCursor(key) {
        const cacheKey = `${key}::cursor`;
        return stateMap.get(cacheKey) || ""
    },

    async getProfile(userId) {
        const userExpirationKey = `user::expiration`

        const expiration = stateMap.get(userExpirationKey) || { createdAt: 0 };
        if (expiration && Date.now() - expiration.createdAt > ttlMs) {
            this.invalidate(`user`);
            stateMap.set(userExpirationKey, { createdAt: Date.now() });
        }

        const cacheKey = `user::${userId}`;
        if (stateMap.has(cacheKey)) {
            return stateMap.get(cacheKey);
        }
        const result = await api.getProfile({}, userId);
        stateMap.set(cacheKey, result);
        return result;
    },

    async getUsers(userId) {
        const cursor = this.getCursor('users')
        if (cursor === endCursor) {
            return []
        }

        const cacheKey = `users::${userId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getUsers({}, {userId:userId, limit:defaultLimit, cursor:cursor});

        result.users = result.users.filter(user => user.id !== userId);

        stateMap.set(cacheKey, result.users);
        this.setCursor('users', result.cursor)
        return result.users;
    },

    async uploadImage(imgFile) {
        const hashKey = await api.uploadImage({}, imgFile);
        const owner = api.getOwnerProfile();
        const cacheKey = `image::${owner.user_id}::${hashKey}`;
        stateMap.set(cacheKey, { createdAt: Date.now(), value: imgFile});
        return hashKey;
    },

    async getImage({userId, key}) {
        const imageTtlMs = 6_000_000; // 100 min

        const cacheKey = `image::${userId}::${key}`;
        if (stateMap.has(cacheKey)) {
            return stateMap.get(cacheKey)?.value;
        }
        const result = await api.getImage({}, {userId:userId, key:key});
        stateMap.set(cacheKey, { createdAt: Date.now(), value: result});
        for (const k of stateMap.keys() || []) {
            if (k.startsWith('image') && (Date.now() - stateMap.get(k)?.createdAt > imageTtlMs)) {
                stateMap.delete(k);
            }
        }
        return result;
    },

    async getMyTimeline() {
        const cursor = this.getCursor('timeline')
        if (cursor === endCursor) {
            return []
        }

        const timelineExpirationKey = `timeline::expiration`

        const expiration = stateMap.get(timelineExpirationKey) || { createdAt: 0 };
        if (expiration && Date.now() - expiration.createdAt > ttlMs) {
            this.invalidate(`timeline`);
            stateMap.set(timelineExpirationKey, { createdAt: Date.now() });
        }

        const cacheKey = `timeline::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) {
            return stateMap.get(cacheKey);
        }
        const result = await api.getMyTimeline({}, {limit:defaultLimit, cursor:cursor});
        stateMap.set(cacheKey, result.tweets);
        this.setCursor('timeline', result.cursor)

        return result.tweets;
    },

    async getTweets(userId) {
        const cursor = this.getCursor('tweets')
        if (cursor === endCursor) {
            return []
        }

        const cacheKey = `tweets::${userId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) {
            return stateMap.get(cacheKey);
        }
        const result = await api.getTweets({}, {userId:userId, limit:defaultLimit, cursor:cursor});
        stateMap.set(cacheKey, result.tweets);
        this.setCursor('tweets', result.cursor)

        return result.tweets;
    },

    async createTweet({text, imageKey}) {
        const tweet = await api.createTweet({}, {text:text, imageKey:imageKey});
        this.invalidate(`timeline`);
        this.invalidate(`tweets::${tweet.user_id}`);
        const cacheKey = `tweet::${tweet.user_id}::${tweet.id}`;
        stateMap.set(cacheKey, tweet)
        return tweet;
    },

    async deleteTweet({userId, tweetId}) {
        const result = await api.deleteTweet({}, {userId:userId, tweetId:tweetId});
        this.invalidate(`timeline`);
        this.invalidate(`tweets::${userId}`);
        stateMap.delete(`tweet::${userId}::${tweetId}`)
        return result;
    },

    async getTweet({userId, tweetId}) {
        const cacheKey = `tweet::${userId}::${tweetId}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getTweet({}, {userId:userId, tweetId:tweetId});
        stateMap.set(cacheKey, result);
        return result;
    },

    async followUser(profileId) {
        const result = await api.followUser({}, profileId);
        const cacheKey = `isFollowed::${profileId}`;
        stateMap.set(cacheKey, {})
        this.invalidate(`followers::${profileId}`);
        this.invalidate(`followees::${profileId}`);
        return result;
    },

    isFollowed(profileId) {
        const cacheKey = `isFollowed::${profileId}`;
        return stateMap.has(cacheKey);
    },

    isFollower(profileId) {
        const cacheKey = `isFollower::${profileId}`;
        return stateMap.has(cacheKey)
    },

    async unfollowUser(profileId) {
        const result = await api.unfollowUser({}, profileId);
        stateMap.delete(`isFollowed::${profileId}`);
        this.invalidate(`followers::${profileId}`);
        this.invalidate(`followees::${profileId}`);
        return result;
    },

    async getFollowers(userId) {
        const cursor = this.getCursor('followers')
        if (cursor === endCursor) {
            return []
        }

        const cacheKey = `followers::${userId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const followers = await api.getFollowers({}, {userId:userId, limit:defaultLimit, cursor:cursor});

        followers.users = followers.users.filter(user => user.id !== userId);

        stateMap.set(cacheKey, followers.users);
        for (const f of followers || []) {
            const followerKey = `isFollower::${f.follower}`;
            stateMap.set(followerKey, {})
        }
        this.setCursor('followers', followers.cursor)

        return followers.users;
    },

    async getFollowees(userId) {
        const cursor = this.getCursor('followees')
        if (cursor === endCursor) {
            return []
        }

        const cacheKey = `followees::${userId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getFollowing({}, {userId:userId, limit:defaultLimit, cursor:cursor});

        result.users = result.users.filter(user => user.id !== userId);

        stateMap.set(cacheKey, result.users);
        this.setCursor('followees', result.cursor)

        return result.users;
    },

    async getTweetStats(tweetId) {
        // const cursor = this.getCursor('tweetstats') // TODO
        const cursor = ""

        const cacheKey = `tweetstats::${tweetId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getTweetStats({}, {tweetId:tweetId, limit:defaultLimit, cursor:cursor});
        stateMap.set(cacheKey, result);
        // this.setCursor('tweetstats', result.cursor)

        return result;
    },

    // create reply
    async replyTweet({rootId, parentId, parentUserId, text}) {
        const reply = await api.replyTweet(
            {}, {rootId:rootId, parentId:parentId, parentUserId:parentUserId, text:text},
            );
        const cacheKey = `reply::${rootId}::${reply.id}`;
        stateMap.set(cacheKey, reply);
        this.invalidate(`replies::${rootId}`)
        this.invalidate(`tweetstats::${rootId}`)
        return reply;
    },

    async getReplies({rootId, parentId}) {
        const cursor = this.getCursor('replies')
        if (cursor === endCursor) {
            return []
        }

        const cacheKey = `replies::${rootId}::${parentId}::${defaultLimit}::${cursor}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getReplies(
            {},
            {rootId:rootId, parentId:parentId, limit:defaultLimit, cursor:cursor},
        );
        stateMap.set(cacheKey, result.replies);
        this.setCursor('replies', result.cursor)
        return result.replies;
    },

    async getReply({rootId, replyId}) {
        const cacheKey = `reply::${rootId}::${replyId}`;
        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);
        const result = await api.getTweetReply({}, {rootId:rootId, replyId:replyId});
        stateMap.set(cacheKey, result);
        return result;
    },

    async deleteReply({userId, rootId, replyId}) {
        const result = await api.deleteTweetReply({}, {userId:userId, rootId:rootId, replyId:replyId});
        const cacheKey = `reply::${rootId}::${replyId}`;
        stateMap.delete(cacheKey);
        this.invalidate(`tweetstats::${rootId}`)
        this.invalidate(`replies::${rootId}`)
        return result;
    },

    async likeTweet(tweetId) {
        const result = await api.likeTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async unlikeTweet(tweetId) {
        const result = await api.unlikeTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async retweetTweet({tweetId, userId, username, text}) {
        const result = await api.retweetTweet(
            {},
            {tweetId:tweetId, userId:userId, username:username, text:text},
            );
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async unretweetTweet(tweetId) {
        const result = await api.unretweetTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async createChat(otherUserId) {
        const chat = await api.createChat({}, otherUserId);
        this.invalidate('chats')
        const cacheKey = `chat::${chat.id}`
        stateMap.set(cacheKey, chat)
        return chat;
    },

    async getChat(chatId) {
        const cacheKey = `chat::${chatId}`

        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);

        const chat = await api.getChat({}, chatId);
        stateMap.set(cacheKey, chat)
        return chat;
    },

    async getChats() {
        const cursor = this.getCursor('chats')
        if (cursor === endCursor) {
            return []
        }

        const owner = api.getOwnerProfile();
        const cacheKey = `chats::${owner.user_id}::${defaultLimit}::${cursor}`

        if (stateMap.has(cacheKey)) return stateMap.get(cacheKey);

        const chats = await api.getChats({}, {limit:defaultLimit, cursor:cursor});

        chats.chats = chats.chats.filter(chat => chat.other_user_id !== owner.user_id && chat.owner_id !== owner.user_id);

        stateMap.set(cacheKey, chats)
        this.setCursor('chats', chats.cursor)

        return chats.chats;
    },
};
